*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: #fff;
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-size: 26px;
    margin-right: 15px;
}

body,html{
    background-image: url("img/Messi.jpg");
    background-color:rgb(0, 74, 22);
    background-size: initial;
    background-position:center center; /*respecto a body*/
    background-repeat: no-repeat;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.calculadora{
    display: flex;
    background-color: rgba(0, 0, 0, 0.534);
    border: 10px outset rgb(176, 176, 176);
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 350px;
    height: 600px;
    border-radius: 50px;
    padding: 35px;
    margin: 25px auto;
    
}

.fila{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 350px;
    height: 80px;
}